}

.list-books-content {
padding: 0 0 80px;
flex: 1;
/*background: #b049e6;*/
}

.bookshelf {
padding: 0 10px 20px;
}

@media (min-width: 600px) {
.bookshelf {
  padding: 0 20px 40px;
}
}

.bookshelf-title {
border-bottom: 1px solid #dedede;
}

.bookshelf-books {
text-align: center;
}

.open-search {
position: fixed;
right: 25px;
bottom: 25px;
}

.open-search button {
display: block;
width: 50px;
height: 50px;
border-radius: 50%;
background: #2e7d32;
background-image: url('./icons/add.svg');
background-repeat: no-repeat;
background-position: center;
background-size: 28px;
box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
font-size: 0;
outline: 0;
}

.open-search button:hover {
background-color: rgb(0, 102, 0);
}


/* search page */

.search-books-bar {
position: fixed;
width: 100%;
top: 0;
left: 0;
z-index: 5;
display: flex;
box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 0 6px rgba(0,0,0,0.23);
}

.search-books-input-wrapper {
flex: 1;
background: #b049e6;
}

.search-books-bar input {
width: 100%;
padding: 15px 10px;
font-size: 1.25em;
border: none;
outline: none;
}

.close-search {
display: block;
top: 20px;
left: 15px;
width: 50px;
height: 53px;
background-image: url('./icons/arrow-back.svg');
background-position: center;
background-repeat: no-repeat;
background-size: 28px;
font-size: 0;
border: none;
outline: none;
}

button:active {
border: red;
outline: none;
}

.search-books-results {
padding: 80px 10px 20px;
}

